#top-navigation {
    background-color: #2B3241;
    border: none;
    border-radius: 0;
    position: fixed;
    height: 50px;
    width: 100vw;
}

.navbar {
    margin-bottom: 0;
}

.navbar-default .navbar-brand a:hover,
.navbar-default .navbar-nav>li>a:hover {
    color: #FBA53D;
    text-decoration: none;
}

.navbar-default .navbar-brand {
    font-family: 'Shadows Into Light Two', cursive;
}

.navbar-default .navbar-nav>li {
    height: 100px;
    display: flex;
    align-content: flex-end;
}

.navbar-default .navbar-nav>li>a {
    font-family: 'Arimo', sans-serif;
    color: white;
}

.navbar-default .navbar-brand a:focus,
.navbar-default .navbar-nav>li>a:focus{
    color: #FBA53D;
}

.left-image img {
    height: calc(100vh - 50px);
    position: relative;
    top: 50px;
    left: 0;
    bottom: 0;
}

.content {
    height: calc(100vh - 50px);
    width: 100vw;
    margin-top: 50px;
    margin-right: 100px;
}

.main {
    display: flex;
    width: 100%;
    flex-direction: row;
}

aside {
    background-color: #454D5F;
    width: 100px;
    height: calc(100vh - 50px);
    position: fixed;
    right: 0;
    top: 50px;
    margin: 0;
}

aside .nav>li>a {
    padding: 20px 15px 0 15px;
    border-bottom: 1px solid black;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
}

aside .nav>li>a>p {
    font-size: 1.4rem;
    color: #7D8D9F;
    margin-top: 5px;
    text-align: center;
}

.fa-lightbulb {
    color: #C1915C;
}

.fa-question-circle {
    color:#827F71;
}

.fa-smile-wink {
    color:#648D73;
}

.fa-hand-peace {
    color: #B5A07E
}

.fa-laptop {
    color: #587A78
}

@media (max-width: 900px), (max-height: 500px) {
    .left-image img {
        display:none;
    }
}

.asset {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
}

.asset h2 {
    font-family: 'Shadows Into Light Two', cursive;
    font-weight: bold;
    font-size: 2.6rem;
    text-transform: uppercase;
    color: #487ffc;
    text-align: center;
}

.asset p {
    font-family: 'Arimo', sans-serif;
    text-align: center;
    font-size: 1.8rem;
}

.asset .example {
    font-size: 1.5rem;
    text-align: justify;
}

.asset .subheader {
    font-weight: bold;
}

.asset img {
    width: 80%;
}

.asset span {
    font-family: 'Shadows Into Light Two', cursive;
    font-weight: 900;
    color: #487ffc;
}

.asset .see-example {
    text-align: center;
}
.flipper p {
    margin: 25px auto;
    font-weight: bold;
}

.flip-container {
    -webkit-perspective: 1000px;
            perspective: 1000px;
    margin: 0 auto;
}

.flip-container:hover .flipper, .flip-container.hover .flipper {
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}

.flip-container, .front, .back {
    width: 100%;
    height: 360px;
    overflow:hidden;
    box-sizing: border-box;
}

.flipper {
	-webkit-transition: 0.6s;
	transition: 0.6s;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
    position: relative;
}

.front, .back {
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	position: absolute;
	top: 0;
    left: 0;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
}

.front {
	z-index: 2;
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
}

.back {
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}

.flip-container img {
    margin: 0 auto;
}

@media (max-width: 700px) {
    .flip-container, .front, .back {
        height: 250px;
    }
}
#home {
    margin-top: 40px;
    margin-right: 80px;
    font-family: 'Arimo', sans-serif;
    width: 100%;
    display: flex;
    justify-content: center;
}

#home span {
    font-family: 'Shadows Into Light Two', cursive;
    font-weight: 900;
    color: #487ffc;
}
  
#home p {
    max-width: 700px;
    font-size: 30px;
}
.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    padding-top: 30px;
}

.contact .subheader {
    max-width: 700px;
    text-align: center;
    font-size: 2rem;
}

.contact span {
    font-family: 'Shadows Into Light Two', cursive;
    font-weight: 900;
    color: #487ffc;
}

.contact .contact-options {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.contact .contact-option a p {
    margin: 10px;
    font-size: 1.6rem;
}

.contact-option a:hover {
    color: #FBA53D;
    text-decoration: none;
}
